function App() {
  let wordNumber = VNnum2words(101800);
  let words = wordNumber.charAt(0).toUpperCase() + wordNumber.slice(1);
  const [file, setFile] = useState(null);
  const onChangeHandler =  event => {
    setFile(event.target.files[0])
    
    
  
  }

  const onclickUploadFile = () => {
    const data = new FormData()
    data.append('file', file)
    axios({
      url: "http://localhost:3000/api/containers/uploadFile",
      method: "POST",
      data: data
    });
  }
 
  return (
    <div style={{ padding: 10 }}>
      <button type="button" style={{}} onClick={onclickDownload}>
        download long report by file
      </button>
      <button type="button" style={{ margin: 10 }} onClick={onclickRender}>
        render long report by file
      </button>

      <button type="button" style={{}} onClick={onclickDownloadShort}>
        download short report by merge files
      </button>
      <button type="button" style={{ margin: 10 }} onClick={onclickRenderShort}>
        render short report by merge files
      </button>
      <button type="button" style={{}} onClick={onclickStreamDownload}>
        download long report by stream
      </button>
      <button type="button" style={{ margin: 10 }} onClick={onclickStreamRender}>
        render long report by stream
      </button>

      <br />
      <button type="button" style={{}} onClick={onclickInvoiceDownload}>
        download invoice reports
      </button>

      <button type="button" style={{ margin: 10 }} onClick={onclickInvoiceRender}>
        render  invoice reports
      </button>
      <div> Convert Number to VND: {words} đồng </div>

      <button type="button" style={{ margin: 10 }} onClick={onclickDownloadExcel}>
        download excel file with header
      </button>
      <button type="button" style={{ margin: 10 }} onClick={onclickDownloadExcelWithTitle}>
        download excel file with title
      </button>
      <button type="button" style={{ margin: 10 }} onClick={onclickCreatFolder}>
        creat folder
      </button>
      <input type="file" name="file" onChange={onChangeHandler} />
      <button type="button" style={{ margin: 10 }} onClick={onclickUploadFile}>
        upload a file
      </button>
     

    </div>
  );
}

export default App;
